import React from 'react';

const Content = ({Name, Price, Components}) =>{
    return(
        <div>
            <div className="subheader">
                <h4>{Name}</h4>
                <p>{Price}</p>
            </div>
            <div className="components">
                {Components.map(component =>
                <p>{component}</p>)}
            </div>

        </div>
    )

}


const Restaurant = ({RestaurantName, MenusForDays}) =>{
    return(MenusForDays.length === 0 || MenusForDays[0]["SetMenus"][0] == null ? <div></div>:
        <div className="container">
            <div className="restaurant-header">
                <h2>{RestaurantName}</h2>
            </div>
            <div>
                {MenusForDays[0]["SetMenus"].map(article =>
                    <Content {...article}/>
                )
                }
            </div>


        </div>
    )
}

export default Restaurant;