import React, {useEffect, useState} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSpinner} from "@fortawesome/free-solid-svg-icons";
import Restaurant from "./Components/Restaurant";



function App() {

  const [restaurantList, setRestaurantList] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api').then(res => res.json())
        .then(data => {
          console.log(data)
          setRestaurantList(data);
          setLoading(false)
        })
  }, [])

  return (loading ?
          <div>
            <FontAwesomeIcon className="spinner" icon={faSpinner} size="6x" flip="vertical" color="white" spin/>
          </div>
          :
          <div>
              <header>
                  <h1>Päivän tarjonta</h1>
                  <p>{new Date().toLocaleDateString('fi', {weekday: 'short', day: 'numeric', month: 'numeric'})}</p>
                  <div id="triangle-down"></div>
              </header>

              <div className="flex">
                  {restaurantList.map(restaurant =>
                      <Restaurant {...restaurant}/>
                  )}
              </div>
              <footer>
                  <p>Sivusto ei vastaa tietojen oikeellisuudesta.</p>
                  <p>Varmista tiedot aina ravintolalta.</p>
                  <p>© 2020 M.T</p>
              </footer>
          </div>
  );
}

export default App;
